<template lang="html">
  <v-app>
    <v-container class="mainContainer margtop">
      <v-layout row class="text-xs-center pic mb-5" v-for="(item,i) in pics.slike" :key="i">
          <clazy-load  :src="getPic(item)" class="pictrans" ref="guda">
            <v-flex class="gudica" slot="image">
              <img :src="getPic(item)" slot="image" >
            </v-flex>
            <v-flex slot="placeholder">
              <div>LOADING</div>
            </v-flex>
          </clazy-load>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    pics() {
      return this.$store.getters.LoadedAlbum(this.id)
    }
  },
    methods: {
      getPic(pic) {
        return require('@/assets/img/' + pic)
      }
    }
  }
</script>

<style scoped lang="css">

</style>



